.store{
        text-align: center;
    padding: 10px 30px 25px;
    border-radius: 40px;
    position: relative;
}

.store a{
    text-decoration: none;
}

.store a i{
    font-size: 25px;
}

.paper-card{
    margin-top: 7px;
    margin-bottom: 7px;
    border-top: 2px solid #4a5903;
    position: absolute;
    overflow: hidden;
    height: 50px;
    width: 70%;
    right: 15%;
    bottom: -40px;
}

.paper-card > div{
    position: absolute;
    background-color: #fff;
    width: 100%;
    max-height: 50px;
    transform: translate3d(0, -38px, 0);
    box-sizing: border-box;
    padding: 10px 5px;
    z-index: 2;
    box-shadow: 0 8px 7px 1px rgba(0, 0, 0, .55);
    transition: .4s ease all;
}

.paper-card > div:after{
    color: #fff;
    content: " ";
    font-size: 18px;
    align-items: center;
    padding-bottom: 10px;
    bottom: -10px;
    right: 0;
    position: absolute;
    width: 100%;
    display: block;
    margin: auto;
    background: linear-gradient(30deg, rgba(0, 0, 0, 0) 40px, #fff 40px), linear-gradient(-45deg, rgba(0, 0, 0, 0) 40px, #fff 40px), linear-gradient(45deg, rgba(0, 0, 0, 0) 34px, #fff 34px), linear-gradient(-45deg, rgba(0, 0, 0, 0) 34px, #fff 34px);
    background-size: 7px calc(100% + 40px);
}

.paper-card > div > span{
    font-size: 12px;
    direction: rtl;
}

.store:hover .paper-card > div{
    transform: translate3d(0, 0, 0);
}