.location{
    margin-top: 50px
}

.location a{
    background-color: #f0002f;
    color: #fff;
    font-size: 14px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    width: 240px;
    text-align: center;
    text-decoration: none;
    padding: 15px 30px;
    transition: .3s ease;
}

.location:hover a,i{
    color: #cbcbcb !important;
}