.cursor-pointer{
    cursor: pointer;
}

.cursor-default{
    cursor: default;
}

.text-dangered{
    color: #a4161c !important;
    font-size: 20px !important;
}

.fa-times{
    cursor: pointer !important56;
}

.icon{
    font-size: 26px !important;
}

.myheader{
    position: sticky;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 5;
}

.text-text{
    position: relative;
    z-index: 1;
}

.text-text span{
    position: relative;
    z-index: 1;
    background-color: #fff;
    padding: 0 15px;
}

.nav-link::selection{
    background-color: transparent;
}

.text-text::after{
    content: "";
    z-index: 0;
    position: absolute;
    width: 100%;
    left: 0;
    top: 50%;
    height: 1px;
    background-color: black;
}

.mycard{
    transition: 0.4s;
}

.mycard:hover{
    box-shadow: 0 0 18px 2px #7b7b7b;
}

.mycard .card-body button{
    transition: all 0.6s;
    opacity: 0;
}

.mycard:hover .card-body button{
    opacity: 1;
}

@media (min-width: 600px){
    .nav-item{
        cursor: pointer !important;
        position: relative;
    }

    .section-media-md .nav-link{
        font-size: 16px !important;
    }

    .section-media-md .nav-link i {
        font-size: 20px;
    }

    .active-bar::after{
        width: 25px;
        height: 25px;
        left: 50%;
        transform: rotate(45deg) translate(-50%, 0);
        bottom: -40px;
        border-radius: 5px;
        content: "";
        position: absolute;
        border: 0;
        background: #343a40;
        box-shadow: 8px 6px 9px -5px rgba(0, 0, 0, .57)
    }
}

@media (max-width: 599px){
    .section-media-mobile{
        position: relative;
    }

    .option-active{
        font-size: 20px;
    }

    .mycard{
        padding: 0 !important;
        margin: 15px 0 0 0 !important;
    }

    .card-img-top{
        height: 100%;
    }

    .card-img-top img{
        height: 100%;
        width: 100%;
    }

    .card-text,.card-title{
        text-align: right !important;
    }

    .card-price{
        text-align: center !important;
        padding-top: 30px;
    }

    .menu-active-mobile{
        position: fixed;
        z-index: 7;
        left: 0;
        top: 0;
        width: 100%;
        background-color: #fff;
        list-style: none;
        text-align: center;
        color: black;
        padding: 15px 0;
        box-shadow: 2px 2px 100px 24px black;
        cursor: default !important;
    }

    .menu-active-mobile li{
        transition: 0.3s;
    }

    .nav-item:hover{
        background-color: #C00A27;
        color: white;
    }

    .active-bar{
        background-color: #C00A27;
        color: white;
    }
}