.pizza-header {
    display: flex;
    justify-content: center;
}

.pizza-header > img{
    height: auto;
    margin-top: 50px;
    width: 80%;
    padding-bottom: 10px;
    animation: transit 20s infinite;
}

@keyframes transit {
    0%{
        transform: scale(1);
    }
    25%{
        transform: scale(0.9);
    }
    50%{
        transform: scale(0.8);
    }
    75%{
        transform: scale(0.7);
    }
    100%{
        transform: scale(1);
    }
}