.mcard{
    width: 25% !important;
    padding: 20px;
    transition: .3s ease all;
}

.mcard:hover{
    cursor: default;
    box-shadow: 0 0 10px -1px #727272;
    border-radius: 2px;
}

.mcard-header{
    text-align: center;
}

.mcard-body{
    margin: 5px 0 0;
    color: #7b7b7b;
    font-size: 13px;
}

.mcard-header > h4 {
    font-size: 14px;
}

.mcard-header > i {
    font-size: 75px;
    color: #777;
    margin-bottom: 20px;
}

@media (max-width: 959px) {
    .mcard{
        width: 100% !important;
    }
}