.main-article{
    padding: 45px 0;
    background-color: #F5F5F5;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    direction: rtl;
    text-align: center;
}

.main-article h3 > span{
    font-size: 33px;
    font-weight: bold;
    font-style: italic;
}

.main-article > div > p{
    font-size: 14px;
    color: #8b8b8b;
    font-style: italic;
}