.nav-menu{
    direction: rtl;
    font-size: 15px;
}

.nav-link-menu{
    position: relative !important;
}

.nav-link-menu:after{
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 0;
    height: 2px;
    background-color: #FFAD00;
    border-radius: 10px;
    transition: linear .2s;
}

.nav-item:hover .nav-link-menu:after{
    width: 30%;
    right: 35%;
}

.active-menu:after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 35%;
    width: 30%;
    height: 2px;
    background-color: #FFAD00;
    border-radius: 10px;
}

@media (max-width: 959px){
    .nav-menu{
        width: 45%;
        height: 100%;
        display: block;
        position: fixed;
        top: 0;
        background-color: #fff;
        overflow-y: auto;
        transition: right 0.6s;
        z-index: 20;
    }

    .nav-item-menu{
        display: block;
        width: 100%;
        height: 30px;
        margin: 10px 0;
    }

    .nav-link-menu{
        color: black !important;
        text-align: center;
    }

    .nav-item:hover .nav-link-menu{
        background-color: #C00A27;
        color: white !important;
    }
}